___CSS_LOADER_EXPORT___.push([module.id, `nav {
    width: clamp(260px, 20vw, 270px);
    background-color: var(--muted-bg-color);
    border-right: 1px solid var(--outline-color);
    padding: 2rem 1.5rem;

    display: grid;
    grid-template-rows: auto 1fr;

    position: sticky;
    top: 0;
    align-self: start;
    height: 100vh;

    transition: var(--default-transition);
}

#shrink-nav-button {
    background-color: var(--muted-bg-color);
    border: 1px solid var(--outline-color);
    border-radius: 100%;
    width: 35px; 
    height: 35px; 

    display: flex;
    justify-content: center;
    align-items: center;

    position: absolute;
    left: clamp(calc(260px - 17.5px), calc(20vw - 17.5px), calc(270px - 17.5px));
    top: 2rem;

    transition: var(--default-transition);
}
#shrink-nav-button:hover {
    cursor: pointer;
    transform: scale(1.1);
}

#shrink-nav-button svg {
    fill: var(--primary-color);
    min-width: 18px;
    min-height: 18px;
    vertical-align: middle;

    transition: var(--default-transition);
}
#shrink-nav-button:hover svg { fill: var(--text-color); }

/* Hidden navbar styles */
nav.hidden { 
    width: 80px;
    padding: 1.5rem 1rem;
    justify-items: center;
}

nav.hidden #profile img {
    width: 35px;
    height: 35px;
}

nav.hidden #shrink-nav-button { 
    border-radius: 40%;

    width: 10px;
    top: 1.5rem;
    left:  70px;
}

nav.hidden #profile div, 
nav.hidden #main-nav-links p { display: none; }
nav.hidden #shrink-nav-button svg { transform: rotate(180deg); }
nav.hidden #main-nav-links svg { min-width: 20px; }

#main-nav-links {
    margin: 2rem 0;

    display: flex;
    flex-direction: column;
}

nav button {
    color: var(--primary-color);
    background: none;
    border: none;
    border-radius: 10px;
    padding: 10px;

    font-size: 0.9rem;

    display: flex;
    align-items: center;
    gap: 1rem;

    transition: var(--default-transition);
}
nav button:hover {
    cursor: pointer;
    color: var(--text-color);
}
nav button.active {
    background: rgba(255, 255, 255, 0.1);
    color: var(--text-color);
}

nav button svg {
    width: 20px;
    height: 20px;

    fill: var(--primary-color);
    vertical-align: middle;

    transition: var(--default-transition);
}
nav button:hover svg { fill: var(--text-color); }

@media only screen and (max-width: 900px) {
    nav, nav.hidden {
        padding: 0;
        height: 60px;
        width: 100%;
        border-top: 1px solid var(--outline-color);

        position: fixed;
        bottom: 0;
        top: unset;

        z-index: 1;
    }

    #shrink-nav-button { display: none; }
    nav #profile { display: none; }

    #main-nav-links, nav.hidden #main-nav-links {
        width: 100%;
        margin: 0;

        flex-direction: row;
        justify-content: space-evenly;
    }
    
    #main-nav-links button, nav.hidden #main-nav-links button { 
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: 0; 

        width: 100%;
    }

    #main-nav-links button p, nav.hidden #main-nav-links button p {         
        display: block;
        font-size: 80%;
    }

    #main-nav-links svg {
        width: 20px;
        height: 20px;
    }
}`, "",{"version":3,"sources":["webpack://./src/styles/nav.css"],"names":[],"mappings":"AAAA;IACI,gCAAgC;IAChC,uCAAuC;IACvC,4CAA4C;IAC5C,oBAAoB;;IAEpB,aAAa;IACb,4BAA4B;;IAE5B,gBAAgB;IAChB,MAAM;IACN,iBAAiB;IACjB,aAAa;;IAEb,qCAAqC;AACzC;;AAEA;IACI,uCAAuC;IACvC,sCAAsC;IACtC,mBAAmB;IACnB,WAAW;IACX,YAAY;;IAEZ,aAAa;IACb,uBAAuB;IACvB,mBAAmB;;IAEnB,kBAAkB;IAClB,4EAA4E;IAC5E,SAAS;;IAET,qCAAqC;AACzC;AACA;IACI,eAAe;IACf,qBAAqB;AACzB;;AAEA;IACI,0BAA0B;IAC1B,eAAe;IACf,gBAAgB;IAChB,sBAAsB;;IAEtB,qCAAqC;AACzC;AACA,+BAA+B,uBAAuB,EAAE;;AAExD,yBAAyB;AACzB;IACI,WAAW;IACX,oBAAoB;IACpB,qBAAqB;AACzB;;AAEA;IACI,WAAW;IACX,YAAY;AAChB;;AAEA;IACI,kBAAkB;;IAElB,WAAW;IACX,WAAW;IACX,WAAW;AACf;;AAEA;+BAC+B,aAAa,EAAE;AAC9C,oCAAoC,yBAAyB,EAAE;AAC/D,iCAAiC,eAAe,EAAE;;AAElD;IACI,cAAc;;IAEd,aAAa;IACb,sBAAsB;AAC1B;;AAEA;IACI,2BAA2B;IAC3B,gBAAgB;IAChB,YAAY;IACZ,mBAAmB;IACnB,aAAa;;IAEb,iBAAiB;;IAEjB,aAAa;IACb,mBAAmB;IACnB,SAAS;;IAET,qCAAqC;AACzC;AACA;IACI,eAAe;IACf,wBAAwB;AAC5B;AACA;IACI,oCAAoC;IACpC,wBAAwB;AAC5B;;AAEA;IACI,WAAW;IACX,YAAY;;IAEZ,0BAA0B;IAC1B,sBAAsB;;IAEtB,qCAAqC;AACzC;AACA,uBAAuB,uBAAuB,EAAE;;AAEhD;IACI;QACI,UAAU;QACV,YAAY;QACZ,WAAW;QACX,0CAA0C;;QAE1C,eAAe;QACf,SAAS;QACT,UAAU;;QAEV,UAAU;IACd;;IAEA,qBAAqB,aAAa,EAAE;IACpC,eAAe,aAAa,EAAE;;IAE9B;QACI,WAAW;QACX,SAAS;;QAET,mBAAmB;QACnB,6BAA6B;IACjC;;IAEA;QACI,aAAa;QACb,sBAAsB;QACtB,uBAAuB;QACvB,MAAM;;QAEN,WAAW;IACf;;IAEA;QACI,cAAc;QACd,cAAc;IAClB;;IAEA;QACI,WAAW;QACX,YAAY;IAChB;AACJ","sourcesContent":["nav {\n    width: clamp(260px, 20vw, 270px);\n    background-color: var(--muted-bg-color);\n    border-right: 1px solid var(--outline-color);\n    padding: 2rem 1.5rem;\n\n    display: grid;\n    grid-template-rows: auto 1fr;\n\n    position: sticky;\n    top: 0;\n    align-self: start;\n    height: 100vh;\n\n    transition: var(--default-transition);\n}\n\n#shrink-nav-button {\n    background-color: var(--muted-bg-color);\n    border: 1px solid var(--outline-color);\n    border-radius: 100%;\n    width: 35px; \n    height: 35px; \n\n    display: