    --text-color: #f5f5f5;
    --bg-color: #171717;
    --muted-bg-color: #1c1c1c;

    --primary-color: #858585;
    --secondary-color: #1c282A;
    --accent-color: #527e86;

    --outline-color: #2b2b2b;

    --default-transition: all 0.4s ease-in-out;

    --primary-font: 'Inter', sans-serif;
}

* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
}

body {
    background-color: var(--bg-color);
    color: var(--text-color);
    font-family: var(--primary-font);

    display: grid;
    grid-template-columns: auto 1fr;
    min-height: 100vh;
}

/* Profile styles */
#profile {
    display: flex;
    align-items: center;
    gap: 1rem;
}

#profile img {
    width: 40px;
    height: 40px;
    border-radius: 100%;
    border: 1px solid var(--outline-color);
}

#profile h1 { font-size: 1rem; }

#profile p {
    color: var(--primary-color);
    font-size: 0.8rem;
}

/* Footer styles */
footer {
    height: 50px;
    background-color: var(--muted-bg-color);
    margin-top: 2rem;

    display: flex;
    flex-direction: row;
    justify-content: center; 
    align-items: center; 
    gap: 7px;
}

footer svg {
    fill: var(--text-color);
    width: 1.5rem;
    transition: var(--default-transition);
    vertical-align: middle;
}
footer svg:hover {
    fill: var(--primary-color);
    cursor: pointer;
}

/* article styles */
article { 
    padding: 8rem 18% 0 18%; 

    display: grid;
    grid-template-rows: auto 1fr auto;
}

article #profile { display: none; }

#content { 
    display: grid; 
    gap: 2rem;
}

#content header {
    display: flex;
    flex-direction: column;

    gap: 1rem;
}

#greeting { font-size: clamp(2rem, 5vw, 3rem); }
#greeting span { 
    color: #595959; 
    background-image: linear-gradient(0deg, #595959 , #D4D4D4 100%); 
    background-clip: text; 
    -webkit-background-clip: text; 
    -webkit-text-fill-color: transparent;  
}

#location {
    display: flex;
    align-items: center;
    gap: 0.5rem;
}
#location p { color: var(--primary-color); }
#location svg {
    width: 1.5rem;
    fill: var(--primary-color);
    vertical-align: middle;
}

#description { width: 85%; }

#content header #buttons-container {
    display: flex;
    align-items: center;
    gap: 0.5rem;
    margin: 0.3rem 0;
}

#content header button {
    background: none;
    border: 1px solid var(--outline-color);
    border-radius: 5px;
    background-color: var(--muted-bg-color);
    padding: 0.6rem 1.3rem;
    color: var(--text-color);

    transition: var(--default-transition);
}
#content header button:hover {
    cursor: pointer;
    color: rgb(160, 160, 160); 
    
    background-color: rgba(0, 0, 0, 0.1);
    border-color: rgb(33, 33, 33);
}

#content header #contact-me-button { 
    display: flex;
    gap: 0.8rem;
    align-items: center;
    background-color: rgb(41, 41, 41);

    position: relative; 
}
#content header #contact-me-button:hover { background-color: rgb(31, 31, 31); }

@property --angle { 
    syntax: '<angle>';
    inherits: false;
    initial-value: 0deg;
}
#content header #contact-me-button::after, 
#content header #contact-me-button::before {
    content: '';
    position: absolute;
    top: 50%;
    left: 50%;
    z-index: -1;

    height: 115%;
    width: 104%;
    background: conic-gradient(from var(--angle), transparent 20%, rgba(255, 255, 255, 0.9));  
    border-radius: inherit;
    translate: -50% -50%;

    animation: 3s spin linear infinite;
}
#content header #contact-me-button::before {
    filter: blur(2rem);
    opacity: 0.3;

    transition: var(--default-transition);
}
#content header #contact-me-button:hover::before { opacity: 0.1; }

/* Section styles */
#content section h2 { margin-bottom: 1rem; }

/* Tech Stack styles */
#tech-stack { overflow: hidden; }

.scroller {
    -webkit-mask: linear-gradient(
        90deg, 
        transparent 0%, 
        black 20%, 
        black 80%, 
        transparent 100%
    );
    mask: linear-gradient(90deg, transparent 0%, black 20%, black 80%, transparent 100%);
}

.scroller ul {
    display: flex;
    gap: 1rem;
    list-style: none;
    align-items: center;

    flex-wrap: nowrap;
    width: fit-content;
}

#tech-stack-list-1 { animation: scroll 25s linear infinite; }
#tech-stack-list-2 { animation: scroll 22s linear infinite;}

#tech-stack-list-1:hover, #tech-stack-list-2:hover { 
    animation-play-state: paused; 
    cursor: default;
}

.scroller img {
    width: 40px;
    height: 40px;
    filter: brightness(0) invert(1);
}

/* Projects styles */
section#projects {
    display: grid;
    grid-template-columns: 1fr 1fr;
    align-items: center;
}

section > p {
    margin-bottom: 0.5rem;
    font-size: 0.9rem;
    color: var(--primary-color);
    justify-self: right;

    transition: var(--default-transition);
}
section > p:hover {
    cursor: pointer;
    color: var(--accent-color);
}

#projects-container {
    grid-area: 2 / 1 / 3 / 3;

    display: grid;
    
    gap: 1rem;
    grid-auto-rows: 230px;
    grid-template-columns: repeat(auto-fit, minmax(300px, 1fr));
    height: 230px;
    overflow: hidden;
}

.project-card {
    position: relative;

    background-color: azure;
    border-radius: 10px;
    transition: var(--default-transition);
    box-shadow: inset 0px 20px 20px -10px rgba(0,0,0,0.5),
                inset 0px -70px 50px -10px rgba(0,0,0,0.5); 
}
.project-card:hover {
    cursor: pointer;
}

#project1 { background: no-repeat center/100% url(${___CSS_LOADER_URL_REPLACEMENT_0___}); }
#project2 { background: no-repeat center/110% url(${___CSS_LOADER_URL_REPLACEMENT_1___}); }
#project3 { background: no-repeat center/110% url(${___CSS_LOADER_URL_REPLACEMENT_2___}); }
#project1:hover{
    background-size: 110%;
}
#project2:hover, #project3:hover {
    background-size: 120%;
}

.project-desc {
    position: absolute;
    bottom: 0;

    width: 100%;
    border-radius: 0 0 10px 10px;
    padding: 0.5rem 1rem;
}

@keyframes scroll {
    to { transform: translate(calc(-50% - 0.5rem)); }
}

@keyframes spin {
    from { --angle: 0deg; }
    to { --angle: 360deg; }
}

/* Mobile styles */
@media only screen and (max-width: 900px) {
    body {
        min-height: 100vh;
        grid-template-columns: 1fr;
    }
 
    article { 
        padding: 2rem 10% 0 10%;
        margin-bottom: 60px; 
    }

    article #profile {
        display: flex;
        margin-bottom: 2rem;
    }

    /* Projects Style */
    #projects-container {
        grid-template-rows: repeat(2, 230px);
        grid-template-columns: 1fr;
        height: calc(690px + 2rem);
    }

    #description { width: 100%; }
